<template>
  <div>
    <form
      action="forms/contact.php"
      method="post"
      role="form"
      class="php-email-form mt-4"
    >
      <div class="form-row">
        <div class="col-md-6 form-group">
          <input
            type="text"
            name="name"
            class="form-control"
            id="name"
            :placeholder="$t('sections.contact.form.name')"
            data-rule="minlen:4"
            data-msg="Please enter at least 4 chars"
          />
          <div class="validate"></div>
        </div>
        <div class="col-md-6 form-group">
          <input
            type="email"
            class="form-control"
            name="email"
            id="email"
            :placeholder="$t('sections.contact.form.email')"
            data-rule="email"
            data-msg="Please enter a valid email"
          />
          <div class="validate"></div>
        </div>
      </div>
      <div class="form-group">
        <input
          type="text"
          class="form-control"
          name="subject"
          id="subject"
          :placeholder="$t('sections.contact.form.subject')"
          data-rule="minlen:4"
          data-msg="Please enter at least 8 chars of subject"
        />
        <div class="validate"></div>
      </div>
      <div class="form-group">
        <textarea
          class="form-control"
          name="message"
          rows="5"
          data-rule="required"
          data-msg="Please write something for us"
          :placeholder="$t('sections.contact.form.message')"
        ></textarea>
        <div class="validate"></div>
      </div>
      <div class="mb-3">
        <div class="loading">Loading</div>
        <div class="error-message"></div>
        <div class="sent-message">
          {{ $t("sections.contact.form.messageResponse") }}
        </div>
      </div>
      <div class="text-center">
        <button type="submit">
          {{ $t("sections.contact.form.btnSendMessage") }}
        </button>
      </div>
    </form>
  </div>
</template>
<script>
export default {
  name: "ContactForm",
};
</script>
